.player__progressbar {
    grid-area: line;
    margin-top: 13px;
    display: flex;
    height: 3px;
    background-color: rgba(255, 255, 255, 0.3);
    position: relative;
    cursor: pointer;
}

@media screen and (max-width: 1024px) {
    .player__progressbar {
        margin-top: 11px;
    }
}